import { CrystalFragment } from '@graphql/generated/graphql';

import styles from './Crystals.module.scss';
import { Falsey } from 'lodash';

export interface CrystalsSelectProps {
  crystals: CrystalFragment[];
  onCrystalSelected: (crystal: CrystalFragment) => void;
  selectedMaterialId: Falsey | string;
}

export const CrystalSelect = ({
  crystals,
  onCrystalSelected,
  selectedMaterialId,
}: CrystalsSelectProps) => {
  return (
    <div className={styles.crystals}>
      {crystals?.map((c) => {
        const imgSrc = c.attributes?.image.data?.attributes?.url;
        const isSelectedCrystal = selectedMaterialId === c.id;

        return (
          <div
            className={`${styles.crystal} ${isSelectedCrystal ? styles.selected : ''}`}
            key={c.id}
            onClick={() => {
              onCrystalSelected(c);
            }}
          >
            <img src={imgSrc} />
          </div>
        );
      })}
    </div>
  );
};
