import Image from '@components/ui/Image';
import { FlagFragment } from '@graphql/generated/graphql';
import { FlagCustomiser, useCustomiserStore } from '@store/customiser';

import styles from './NavFlags.module.scss';

export interface NavFlagsSelectProps {
  editFlag?: FlagCustomiser;
  flags: FlagFragment[];
}

export const NavFlagsSelect = ({ editFlag, flags }: NavFlagsSelectProps) => {
  const { addFlag, updateFlag } = useCustomiserStore((state) => state);

  const editFlagId = editFlag?.flag?.id;

  const flagSelected = (flag: FlagFragment) => {
    if (editFlag?.key) {
      updateFlag(editFlag.key, {
        flag: flag,
        edit: true,
      });
    } else {
      addFlag({
        flag: flag,
        decalFreeze: true,
        decalPosition: [-0.01989325641440387, 2.436930108198209, 1.6226380277735046],
        decalOrientation: [
          -0.14799034762565572,
          -0.17866277612764514,
          -0.026487426629834374,
          'XYZ',
        ],
      });
    }
  };

  return (
    <div className={styles.flags}>
      {flags?.map((f) => {
        const editClass = editFlagId === f.id ? styles.edit : '';

        return (
          <div className={`${styles.flag} ${editClass}`} key={f.id} onClick={() => flagSelected(f)}>
            <Image
              className={`${styles.flagImgContainer} ${editClass}`}
              image={f.attributes?.image.data}
            />
          </div>
        );
      })}
    </div>
  );
};
