import Button from '@components/ui/Button';
import { usePortalRef } from '@hooks';
import cn from 'classnames';
import ReactDOM from 'react-dom';

import styles from './NavEditButtons.module.scss';
import ContainedButton from '@components/ui/Button/ContainedButton';

export interface NavEditButtonsProps {
  hasCancel?: boolean;
  className?: string;
  onApply?: () => void;
  disabled: boolean;
  onNext?: false | (() => void);
}

const NavEditButtons = ({
  className,
  disabled,
  onApply,
  hasCancel = true,
  onNext,
}: NavEditButtonsProps) => {
  const rootClassName = cn(styles.root, className);
  const portalRef = usePortalRef('CustomiserNavActions');

  if (!portalRef) {
    return null;
  }

  return ReactDOM.createPortal(
    <div className={rootClassName}>
      <ContainedButton onClick={onApply} isHidden={!hasCancel}>
        Cancel
      </ContainedButton>
      {onNext ? (
        <ContainedButton onClick={onNext} isClickable={!disabled}>
          Next
        </ContainedButton>
      ) : (
        <ContainedButton color='black' onClick={onApply} isClickable={!disabled}>
          Save
        </ContainedButton>
      )}
    </div>,
    portalRef,
  );
};

export default NavEditButtons;
