import { CreateGraphicMutation } from '@graphql/generated/graphql';

const testUploadImageResponse = (): CreateGraphicMutation => {
  return {
    __typename: 'Mutation',
    createGraphic: {
      __typename: 'GraphicEntityResponse',
      data: {
        __typename: 'GraphicEntity',
        id: Math.random().toString(),
        attributes: {
          __typename: 'Graphic',
          name: 'Sample Graphic',
          image: {
            __typename: 'UploadFileEntityResponse',
            data: {
              __typename: 'UploadFileEntity',
              id: Math.random().toString(),
              attributes: {
                __typename: 'UploadFile',
                url: 'examples/add-image/image.png',
                size: 51200, // 50 KB
                width: 800,
                height: 600,
                ext: '.jpg',
                mime: 'image/jpeg',
                name: 'sample.jpg',
                formats: {
                  thumbnail: {
                    url: 'examples/add-image/image.png',
                    width: 200,
                    height: 150,
                  },
                },
              },
            },
          },
        },
      },
    },
  };
};

export default testUploadImageResponse;
