import {
  GraphicPriceEntity,
  GraphicPriceFragment,
  ImageFragment,
  Maybe,
} from '@graphql/generated/graphql';
import Image from '../Image/Image';
import styles from './ImageDecalPreview.module.scss';
import NavDecalAdjust from '@components/nav/NavDecalAdjust copy';
import { FaEdit } from 'react-icons/fa';

interface ImageDecalPreview {
  image?: Maybe<ImageFragment>;
  scale?: number;
  rotation?: number;
  onScale?: (event: number, price?: GraphicPriceEntity) => void;
  onRotate?: (event: number) => void;
  prices?: GraphicPriceFragment[];

  onImgClick?: () => void;
}
const ImageDecalPreview = ({
  image,
  scale,
  rotation,
  onScale,
  onRotate,
  prices,
  onImgClick,
}: ImageDecalPreview) => {
  return (
    <div>
      {image && <div className={styles.title}>IMAGE PREVIEW</div>}
      <div className={`${styles.preview} ${!image ? styles.noImage : ''}`}>
        {image && (
          <span className={styles.imageContainer} onClick={onImgClick}>
            <Image image={image} />
            {/* {onImgClick && (
              <span className={styles.editIcon}>
                <FaEdit />
              </span>
            )} */}
          </span>
        )}
        <div>
          <NavDecalAdjust
            scale={scale}
            rotation={rotation}
            onScale={onScale}
            onRotate={onRotate}
            prices={prices}
            hasImage={!!image}
          />
        </div>
      </div>
    </div>
  );
};

export default ImageDecalPreview;
