import { startTransition, useState } from 'react';
import { NavFlagsSelect } from './NavFlagsSelect';

import Button from '@components/ui/Button';
import { useCustomiserStore } from '@store/customiser';
import NavButtons from '../NavButtons';
import NavDecalAdjust from '../NavDecalAdjust';
import NavEditButtons from '../NavEditButtons';
import styles from './NavFlags.module.scss';
import { NavFlagsFlag } from './NavFlagsFlag';
import { GraphicPriceEntity, useGetFlagsQuery } from '@graphql/generated/graphql';
import { graphQLClient } from '@graphql/graphql-client';
import ContainedButton from '@components/ui/Button/ContainedButton';
import { IoAddOutline } from 'react-icons/io5';
import ImageDecalPreview from '@components/ui/ImageDecalPreview/ImageDecalPreview';

const NavFlags = () => {
  const flags = useCustomiserStore((state) => state.flags);
  const updateFlag = useCustomiserStore((state) => state.updateFlag);
  const editFlag = flags?.find((g) => g.edit);

  const [showSelector, setShowSelector] = useState(false);

  const [page, setPage] = useState(0);
  const maxPage = 1;
  const hasNextPage = page < maxPage;

  const nextPageHandler = () => {
    setPage((cur) => {
      if (!hasNextPage) return cur;
      return (cur += 1);
    });
  };

  const { data: flagQuery } = useGetFlagsQuery(
    graphQLClient,
    {
      pagination: { limit: 500 },
    },
    {
      select: (data) => ({ flags: data.flags?.data, prices: data.graphicPrices?.data }),
    },
  );

  const addFlag = () => {
    setShowSelector(true);
  };

  const setScale = (event: number, price?: GraphicPriceEntity) => {
    const priceValues =
      price?.attributes?.size && price?.attributes?.basePrice
        ? {
            size: price.attributes.size,
            basePrice: {
              ...price.attributes.basePrice,
              quantity: 1,
            },
          }
        : {};

    if (editFlag?.key) {
      updateFlag(editFlag.key, { decalScale: Number(event), ...priceValues });
    }
  };

  const setRotation = (event: number) => {
    startTransition(() => {
      if (editFlag?.key) updateFlag(editFlag.key, { decalRotation: Number(event) });
    });
  };

  const applyFlag = () => {
    if (editFlag?.key) {
      updateFlag(editFlag.key, {
        decalFreeze: false,
        edit: false,
      });
    }

    setPage(0);
    setShowSelector(false);
  };

  return (
    <>
      {showSelector ? (
        <>
          {/* <NavDecalAdjust
            scale={editFlag?.decalScale}
            rotation={editFlag?.decalRotation}
            onScale={setScale}
            onRotate={setRotation}
            prices={flagQuery?.prices}
          /> */}

          {page === 0 && flagQuery?.flags && (
            <NavFlagsSelect editFlag={editFlag} flags={flagQuery.flags} />
          )}

          {page === 1 && (
            <ImageDecalPreview
              image={editFlag?.flag?.attributes?.image.data}
              scale={editFlag?.decalScale}
              rotation={editFlag?.decalRotation}
              onScale={setScale}
              onRotate={setRotation}
              prices={flagQuery?.prices}
            />
          )}
          <NavEditButtons
            disabled={!editFlag}
            onApply={applyFlag}
            onNext={hasNextPage && nextPageHandler}
            hasCancel={false}
          />
        </>
      ) : (
        <>
          <div className={styles.customiserFlags}>
            <div className={styles.addBtnContainer}>
              <ContainedButton onClick={addFlag} color='grey'>
                <IoAddOutline /> &nbsp; Add Flag
              </ContainedButton>
            </div>
            {flags.length ? <div className={styles.itemsTitle}>YOUR FLAGS</div> : ''}
            {flags?.map((flag) => (
              <NavFlagsFlag key={flag.key} flag={flag} setShowSelector={setShowSelector} />
            ))}
          </div>
          {/* <NavButtons /> */}
        </>
      )}
    </>
  );
};

export default NavFlags;
