import {
  GraphicFragment,
  GraphicPriceEntity,
  GraphicPriceFragment,
} from '@graphql/generated/graphql';
import { GraphicCustomiser, useCustomiserStore } from '@store/customiser';
import { useState } from 'react';
import Image from '@components/ui/Image';

import styles from './NavImages.module.scss';
import { NavImagesUpload } from './NavImagesUpload';
import Button from '@components/ui/Button';
import ImageDecalPreview from '@components/ui/ImageDecalPreview/ImageDecalPreview';

export interface NavImagesSelectProps {
  editGraphic?: GraphicCustomiser;
  graphic?: GraphicFragment;

  scale?: number;
  rotation?: number;
  onScale?: (event: number, price?: GraphicPriceEntity) => void;
  onRotate?: (event: number) => void;
  prices?: GraphicPriceFragment[];
}

export const NavImagesSelect = ({
  editGraphic,
  graphic,
  onScale,
  onRotate,
  prices,
  scale,
  rotation,
}: NavImagesSelectProps) => {
  const [currentGraphic, setCurrentGraphic] = useState<GraphicFragment | undefined>(graphic);
  const { addGraphic, updateGraphic } = useCustomiserStore((state) => state);

  const graphicSelected = (graphic: GraphicFragment) => {
    if (editGraphic?.key) {
      updateGraphic(editGraphic.key, {
        graphic,
        edit: true,
      });
    } else {
      addGraphic({
        graphic,
        decalFreeze: true,
        decalPosition: [-0.01989325641440387, 2.436930108198209, 1.6226380277735046],
        decalOrientation: [
          -0.14799034762565572,
          -0.17866277612764514,
          -0.026487426629834374,
          'XYZ',
        ],
      });
    }
  };

  const setGraphic = (graphic: GraphicFragment) => {
    setCurrentGraphic(graphic);
    graphicSelected(graphic);
  };

  return (
    <div className={styles.graphics}>
      {currentGraphic ? (
        <>
          <ImageDecalPreview
            image={currentGraphic.attributes?.image?.data}
            scale={scale}
            rotation={rotation}
            onScale={onScale}
            onRotate={onRotate}
            prices={prices}
            onImgClick={() => setCurrentGraphic(undefined)}
          />
        </>
      ) : (
        <NavImagesUpload setGraphic={setGraphic} />
      )}
    </div>
  );
};
