import { CrystalEntity, CrystalFragment, useGetCrystalsQuery } from '@graphql/generated/graphql';
import { graphQLClient } from '@graphql/graphql-client';
import { CrystalSelect } from './CrystalsSelect';
import { Falsey } from 'lodash';

interface CrystalsProps {
  onCrystalSelected: (crystal: CrystalFragment) => void;
  selectedMaterialId: Falsey | string;
}

const Crystals = ({ onCrystalSelected, selectedMaterialId }: CrystalsProps) => {
  const { data: crystalsQuery } = useGetCrystalsQuery(
    graphQLClient,
    { pagination: { pageSize: 200 } },
    {
      select: (data) => {
        return data.crystals?.data as CrystalEntity[];
      },
    },
  );

  return (
    <>
      {crystalsQuery?.length && (
        <CrystalSelect
          crystals={crystalsQuery}
          onCrystalSelected={onCrystalSelected}
          selectedMaterialId={selectedMaterialId}
        />
      )}
    </>
  );
};

export default Crystals;
